/*    - */
.header__actions .burger {
  background: none;
  border: none;
  cursor: pointer;
  position: relative;
  z-index: 10;
  padding: 0;
  width: 30px; /*     - */
  height: 25px; /*    - */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header__actions .burger span {
  display: block;
  width: 30px; /*   */
  height: 3px; /*   */
  background: #000; /*   */
  border-radius: 3px; /*    */
  transition: all 0.3s ease;
}

.header__actions .burger span::before,
.header__actions .burger span::after {
  content: "";
  display: block;
  width: 30px; /*   */
  height: 3px; /*   */
  background: #000; /*   */
  border-radius: 3px; /*    */
  transition: all 0.3s ease;
}

.header__actions .burger span::before {
  transform: translateY(-8px); /*    */
}

.header__actions .burger span::after {
  transform: translateY(5.2px); /*    */
}

/*     - () */
.header__actions .burger.active span {
  background: transparent; /*    */
}

.header__actions .burger.active span::before {
  transform: rotate(45deg) translateY(2.5px); /*       */
}

.header__actions .burger.active span::after {
  transform: rotate(-45deg) translateY(-2.5px); /*       */
}

.mobile-menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 250px;
  height: 100%;
